import styled from 'styled-components';

export const BUTTON = styled.div`
	
	position:fixed;
	right:20px;
	bottom:20px;
	width:60px;
	height:60px;
	cursor:pointer;
	background-repeat: no-repeat;
	background-size: cover;

	&:hover{
		box-shadow: 0 0px 30px 0 rgba(0, 0, 0, 0.2), 0 0px 30px 0 rgba(0, 0, 0, 0.19);
	}

`;


